* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  padding-top: 122px;
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(121,9,46,0.3883928571428571) 35%, rgba(0,212,255,1) 100%);
}
.container {
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  width: 975px
}
.bg-pd{
  height: 100vh;
}
.pokedex-container{
  background-image: url("../src/pokedex.png");
  background-repeat:no-repeat;
  background-position: center center;
  height: 100vh;
}
.pm-image{
  height:250px;
  width: auto;
  float:right;
  padding-right: 5em;
  margin-top:-4.5em;
}
.pm-img{
  height:100%;
  width:100%;
}
.input-text{
  margin-left: 25em;
  padding-top: 6em;
}
.data{
  color:white;
  margin-left:13.1em;
  padding-top:.5em;
  font-size:xx-large;
  text-shadow: 2px 2px #ff0000;
}
.text{
  color: #2a75bb;
  font-size: xx-large;
  text-shadow: 1px 1px #ffcb05;
}